import React, { Component } from "react";
import { Container } from "react-bootstrap";
import "./Story.css";
import {
  VerticalTimeline,
  VerticalTimelineElement
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import Child from "./icons/Child.png";
import StudentBeforeGraduation from "./icons/StudentBeforeGraduation.png";
import Chip from "./icons/Chip.png";
import Eml from "./icons/Eml.png";
import Internship from "./icons/Internship.png";
import StudentAfterGraduation from "./icons/StudentAfterGraduation.png";
import Scholarship from "./icons/Scholarship.png";
import Lab from "./icons/Lab.png";

class Experiences extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <Container className="Experiences">
        <h1>Experiences</h1>

        <VerticalTimeline className="TimelineMaster">
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            contentArrowStyle={{ borderRight: "7px solid  rgb(33, 150, 243)" }}
            date="Aug, 1996"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={<img src={Child} alt="myLogo" style={{ width: "100%" }} />}
            position={"left"}
          >
            <h3 className="vertical-timeline-element-title">I have Arrived</h3>
            <h7 className="vertical-timeline-element-subtitle">
              Hyderabad, India
            </h7>
            <footer style={{ textAlign: "left" }}>July, 1996</footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="July, 2015"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={
              <img
                src={StudentBeforeGraduation}
                alt="myLogo"
                style={{ width: "100%" }}
              />
            }
          >
            <h3 className="vertical-timeline-element-title">
              Indian Institute of Technology
            </h3>
            <h7 className="vertical-timeline-element-subtitle">
              Chennai, India
            </h7>
            <p>Started pursuing my bachelor's Degree</p>
            <footer style={{ textAlign: "left" }}>July, 2015</footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="2017-2018"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={<img src={Chip} alt="myLogo" style={{ width: "100%" }} />}
          >
            <h3 className="vertical-timeline-element-title">
              Head, Electronics Club
            </h3>
            <h7 className="vertical-timeline-element-subtitle">
              Centre For Innovation, IIT Madras
            </h7>
            <p>
              <ul>
                <li>
                  Electronics Club is a dedicated student interest group that
                  provides the foundation to students in the diverse fields of
                  electronics ranging from designing simple PID control to
                  modern-day Internet of Things
                </li>
                <li>
                  Led a team of 6 coordinators and 60+ students under 7 projects
                  spread over fields like soldier-assist technology, green
                  energy and touch recognition of electromechanical objects
                </li>
              </ul>
            </p>
            <footer style={{ textAlign: "left" }}>
              July, 2017 – July, 2018
            </footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="2017-2018"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={<img src={Eml} alt="myLogo" style={{ width: "100%" }} />}
          >
            <h3 className="vertical-timeline-element-title">
              Head, Extra Mural Lectures
            </h3>
            <h7 className="vertical-timeline-element-subtitle">
              IIT Madras, Chennai, India
            </h7>
            <p>
              <ul>
                <li>
                  Extra Mural Lectures is a team of students which organizes
                  lectures and performances by eminent personalities from all
                  walks of life. His Holiness The 14th Dalai Lama was one of the
                  many exemplary figures hosted by our team
                </li>
                <li>
                  Led a team of 20 students and conducted 19 lectures on topics
                  about global economy, conservation of habitats of migratory
                  birds, indigenous defence technologies, social
                  entrepreneurship among others
                </li>
              </ul>
            </p>
            <footer style={{ textAlign: "left" }}>
              July, 2017 – July, 2018
            </footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="2017-2018"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={<img src={Eml} alt="myLogo" style={{ width: "100%" }} />}
          >
            <h3 className="vertical-timeline-element-title">
              Head, Events, Inter IIT Tech Meet 2018
            </h3>
            <h7 className="vertical-timeline-element-subtitle">
              Centre For Innovation, IIT Madras
            </h7>
            <p>
              <ul>
                <li>
                  Designed and organized an event for augmented soldier-support
                  technology for the 6th edition of Inter-IIT Tech Meet, which
                  is an annual competition of technology oriented challenges
                  where all the 23 IITs participate
                </li>
                <li>
                  Initiated first of its kind collaboration with Defence
                  Research and Development Organization (DRDO) to solve
                  on-ground problems of the army in the fields of drone
                  surveillance, crowd activity monitoring, etc.
                </li>
              </ul>
            </p>
            <footer style={{ textAlign: "left" }}>
              July, 2017 – July, 2018
            </footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="2017-2018"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={
              <img src={Internship} alt="myLogo" style={{ width: "100%" }} />
            }
          >
            <h3 className="vertical-timeline-element-title">
              Antenna Engineer Intern
            </h3>
            <h7 className="vertical-timeline-element-subtitle">
              Aachen, Germany
            </h7>
            <p>
              <ul>
                <li>
                  Conducted experimental work on European Regional Development
                  Fund project – “Packsense” – to monitor food freshness through
                  printed sensors and Near Field Communication interface
                </li>
                <li>
                  Was granted a scholarship of 1850 euros under DAAD-UGC program
                  for this internship.
                </li>
              </ul>
            </p>
            <footer style={{ textAlign: "left" }}>
              May, 2018 – July, 2018
            </footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="July, 2015"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={
              <img
                src={StudentAfterGraduation}
                alt="myLogo"
                style={{ width: "100%" }}
              />
            }
          >
            <h3 className="vertical-timeline-element-title">
              Indian Institute of Technology
            </h3>
            <h7 className="vertical-timeline-element-subtitle">
              Chennai, India
            </h7>
            <br></br>
            <h7>
              Bachelor of Technology in{" "}
              <b>Metallurgical and Materials Engineering</b>
              with minors in <b>Semiconductor Devices</b>
            </h7>
            <p>Graduated with 8.72/10 CGPA</p>
            <footer style={{ textAlign: "left" }}>May, 2019</footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="July, 2015"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={
              <img src={Scholarship} alt="myLogo" style={{ width: "100%" }} />
            }
          >
            <h3 className="vertical-timeline-element-title">
              K C Mahindra Education Trust Scholarship
            </h3>
            <h7 className="vertical-timeline-element-subtitle">India</h7>
            <p>
              Among the 35 in 1500+ students to receive a $6,000 scholarship
              from K C Mahindra Education Trust as an aid for pursuing Master’s
            </p>
            <footer style={{ textAlign: "left" }}>June, 2019</footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="July, 2015"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={
              <img
                src={StudentBeforeGraduation}
                alt="myLogo"
                style={{ width: "100%" }}
              />
            }
          >
            <h3 className="vertical-timeline-element-title">
              Stanford University
            </h3>
            <h7 className="vertical-timeline-element-subtitle">CA, USA</h7>
            <p>
              Started pursuing my Masters's Degree in{" "}
              <b>Material Science and Engineering</b>
            </p>
            <footer style={{ textAlign: "left" }}>July, 2015</footer>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            // date="2017-2018"
            iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
            icon={
              <img src={Internship} alt="myLogo" style={{ width: "100%" }} />
            }
          >
            <h3 className="vertical-timeline-element-title">
              Graduate Research Assistant
            </h3>
            <h7 className="vertical-timeline-element-subtitle">
              Stanford School of Medicine
            </h7>
            <p>
              <ul>
                <li>
                  Current work focusses on non-obtrusive, “sensor-less” sensing
                  of stress through mouse, touchpad interactions and
                  bio-mechanical modelling.
                </li>
                <li>
                  Implemented primitive models to parse and classify various
                  mouse events, and extract relevant features from the data set.
                </li>
              </ul>
            </p>
            <footer style={{ textAlign: "left" }}>January, 2020</footer>
          </VerticalTimelineElement>
        </VerticalTimeline>
      </Container>
    );
  }
}

export default Experiences;
