import React, { Component } from "react";
import { Container } from "react-bootstrap";
import "./Contact.css";
import "react-notifications/lib/notifications.css";
import { FaEnvelope } from "react-icons/fa";
import { FaLinkedin } from "react-icons/fa";
import { IconContext } from "react-icons";
import {
  NotificationContainer,
  NotificationManager
} from "react-notifications";

// https://i1.wp.com/wnynewsnow.com/wp-content/uploads/2018/12/Digital-background.png?fit=1280%2C720&ssl=1
class EndBanner extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  MailIconCLicked = () => {
    console.log("Click happened");
    const el = document.createElement("textarea");
    el.value = "saihari.m@outlook.com";
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
    console.log("Clicked");
    NotificationManager.info(
      "Email-ID copied to clipboard",
      "",
      2000,
      "",
      true
    );
  };

  LinkedinIconClicked = () => {
    window.open(
      "https://in.linkedin.com/in/sai-hari-chandan-morapakala-050000194",
      "_blank"
    );
  };

  render() {
    return (
      <Container fluid={true} className="EndBanner">
        <h1>Get in Touch</h1>
        <IconContext.Provider value={{ size: "2em" }}>
          <Container fluid={false} className="ContactBtnContainer">
            <div className="ContactBtn">
              <FaEnvelope
                className="MailContact"
                onClick={this.MailIconCLicked}
              />
            </div>
            <div className="ContactBtn">
              <FaLinkedin
                className="MailContact "
                onClick={this.LinkedinIconClicked}
              />
            </div>
          </Container>
        </IconContext.Provider>
        <NotificationContainer />
      </Container>
    );
  }
}

export default EndBanner;
